.header_bar {
  display: flex;
  align-items: center;
  position: fixed;
  top: 0;
  left: 250px;
  width: 100%;
  height: 60px;
  background-color: white;
  box-shadow: 0 2px 20px #0129701a;
  z-index: 1000;
  transition: all 0.3s;
}

.burger_btn {
  display: flex;
  height: 60px;
  align-items: center;
  font-size: 32px;
  margin: 0 20px 0 10px;
  cursor: pointer;
}

.menu_close {
  left: 0;
}
